<template>
  <div class="inner">
    <section class="creation">
      <textarea name="" id="data_title" rows="10" v-model="app_data.input" placeholder="новое задание"></textarea>
      <div class="data_group">
        <a href="" :key="index" v-for="(group, index) in app_data.group"
        :class="{active: group.status}" @click.prevent="group.status = !group.status">
          {{group.title}}
        </a>
      </div>
      <a href="" class="save" @click.prevent="save_temp()">добавить</a>
    </section>
    <section class="view" v-if="quest_count - quest_complete > 0">
      <div class="quest" :key="quest.id" v-for="quest in get(false)" @click="execute(quest.id)">
        <h2 class="data_title">{{quest.title}}</h2>
        <div class="data_group">
          <p :key="index" v-for="(group, index) in format_group(quest.group)">{{group}}</p>
        </div>
        <p class="data_date">{{format_date(quest.date)}}</p>
      </div>
    </section>
    <section class="history" v-if="quest_complete > 0">
      <div class="quest" :key="index" v-for="(quest, index) in get(true)">
        <h2 class="data_title">{{quest.title}}</h2>
        <div class="data_group">
          <p :key="index" v-for="(group, index) in format_group(quest.group)">{{group}}</p>
        </div>
        <p class="data_date">{{format_date(quest.date)}}</p>
      </div>
    </section>
    <section class="stat">
      <div v-for="(group, index) in group_stat" :key="index" :style="{width: (group * 5) + '%', backgroundColor: app_data.group[index].color}">
        <p>{{app_data.group[index].title}}</p>
      </div>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'home',
  data() {
    return {
      app_data:{
        group: [
          {title:'сила', status: false, color: '#4B94F2'}, {title:'выносливость', status: false, color: '#33A650'},
          {title:'интелект', status: false, color: '#F2B807'}, {title:'знания', status: false, color: '#F25041'},
        ],
        input: '',
      },
      quest_data: [
        {id: 0, title: "Первое задание", date: new Date, group: [0, 1], status: false}
      ],
      quest_stat: {
        last_update: new Date
      },
      group_stat:[0,5,0,0],
    }
  },
  computed: {
    quest_complete(){return(this.quest_data.filter(quest => {return(quest.status)}).length)},
    quest_count(){return(this.quest_data.length)},
  },
  mounted() {
    this.load();
  },
  updated(){
    this.save();
  },
  methods: {
    load(){
      //quest data
      if (localStorage.quest_data){
        this.quest_data = JSON.parse(localStorage.quest_data);
      }
      //group stat
      if (localStorage.group_stat){
        this.group_stat = JSON.parse(localStorage.group_stat);
      }
    },
    save(){
      localStorage.quest_data = JSON.stringify(this.quest_data);
      localStorage.group_stat = JSON.stringify(this.group_stat);
    },
    save_temp(){
      const id = this.quest_data.length;
      //check empty textarea
      if (this.app_data.input.length > 0){
        let group_index = [];
        this.app_data.group.forEach((group, index) => {
          if (group.status){
            group_index.push(index);
          }
        })
        this.quest_data.push({id: id, title: this.app_data.input, date: new Date, group: group_index, status: false});
        this.app_data.input = '';
      }
    },
    execute(index){
      this.quest_data[index].group.forEach((group) => {
        this.group_stat[group]++;
      })
      this.quest_data[index].status = true;
    },
    get(satus){
      return(this.quest_data.filter(quest =>{ return(quest.status == satus) }));
    },
    format_date(date){
      date = new Date(Date.parse("2019-12-10T09:48:56.463Z"));
      return(date.getDate() + '.' + date.getMonth() + '.' + date.getFullYear());
    },
    format_group(index){
      let string = ["сила", "выносливость", "ум", "знания"];
      let result = [];
      index.forEach(group => {
        result.push(string[group]);
      })
      return(result);
    }
  },
}
</script>

